  created() {
    this.getCateList()
  },
  methods: {
    async getCateList() {
      let { data: res } = await this.$axios.get('categories', {
        params: this.queryParams
      })
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.cateList = res.data.result
      this.total = res.data.total
      this.$message.success('')
    },
    // pagesize
    sizeChange(newSize) {
      this.queryParams.pagesize = newSize
      this.getCateList()
    },
    // pagenum
    currentChange(newPage) {
      console.log(newPage)
      this.queryParams.pagenum = newPage
      this.getCateList()
    },
    // 
    showCateDialog() {
      this.getParentCateList()
      this.addCateDialogVisible = true
    },
    // 
    async getParentCateList() {
      let { data: res } = await this.$axios.get('categories', {
        params: {
          type: 2
        }
      })
      if (res.meta.status !== 200) return this.$message.error('')
      function handle(item) {
        item.forEach(val => {
          val.value = val.cat_id
          delete val.cat_id
          val.label = val.cat_name
          delete val.cat_name
          if (val.children) {
            handle(val.children)
          }
        })
      }
      handle(res.data)
      console.log(res.data)
      this.parentCateList = res.data
    },
    // 
    cascaderChange() {
      if (this.selectedCascader.length > 0) {
        // id
        this.addCateForm.cat_pid = this.selectedCascader[
          this.selectedCascader.length - 1
        ]
        // 
        this.addCateForm.cat_level = this.selectedCascader.length
      } else {
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    addCateSubmit() {
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        let { data: res } = await this.$axios.post(
          'categories',
          this.addCateForm
        )
        if (res.meta.status !== 201) return this.$message.error('')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    }
  }
}
