<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            class="input-with-select"
            v-model="params.query"
            clearable
            @clear="getUserList"
          >
            <el-button slot="append" icon="el-icon-search" @click="searchGetUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4778750">
          <el-button type="primary" @click="dialogVisible=true"></el-button>
        </el-col>
      </el-row>

      <!-- table -->
      <el-table :data="userList" style="width: 100%" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
        <el-table-column prop="mobile" label=""></el-table-column>
        <el-table-column prop="role_name" label=""></el-table-column>
        <el-table-column label="">
          <template v-slot="scope">
            <el-tooltip :content="scope.row.mg_state?'':''" placement="top">
              <!-- active-valuev-modelactive-value="100"
              inactive-value="0"-->
              <el-switch v-model="scope.row.mg_state" @change="userStateChange(scope)"></el-switch>
            </el-tooltip>
            <!-- userListmg_state -->
            {{scope.row.mg_state}}
          </template>
        </el-table-column>

        <el-table-column label="">
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              class="operateColumnButton"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="delUser(scope.row.id)"
            ></el-button>
            <el-tooltip content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="showAssignRoleDialog(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.pagenum"
        :page-sizes="[1,2,5,10]"
        :page-size="params.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="50%" @close="resetDialogForm">
      <!--  -->
      <el-form :model="addUserForm" :rules="userRules" ref="addUserForm" label-width="70px">
        <el-form-item label="" prop="username">
          <el-input v-model="addUserForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addUserForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addUserForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUserSubmit"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
      <el-form :model="editUserForm" :rules="userRules" ref="editUserForm" label-width="80px">
        <el-form-item label="">
          <el-input v-model="editUserForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editUserForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="editDialogVisible= false"> </el-button>
        <el-button type="primary" @click="editUserFormSubmit"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="assignDialogVisible" width="50%">
      <div>
        <p>:{{userInfo.username}}</p>
        <p>:{{userInfo.role_name}}</p>
        <p>
          :
          <el-select v-model="selectRoleId" placeholder="">
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer">
        <el-button @click="editDialogVisible= false"> </el-button>
        <el-button type="primary" @click="assignRole"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    // 
    // var checkEmail=(rule,value,callback)=>{
    //   const regEmail=/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)$/
    //   if(regEmail.test(value)){
    //     return callback()
    //   }
    //   callback(new Error(''))
    // }
    // 
    // var checkMobile = (rule, value, callback) => {
    //   const regMobile = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/
    //   if (regMobile.test(value)) {
    //     return callback()
    //   }
    //   callback(new Error(''))
    // }
    return {
      params: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      userList: [
        {
          id: 500,
          role_name: '',
          username: 'admin',
          create_time: 1486720211,
          mobile: '12345678',
          email: 'adsfad@qq.com',
          mg_state: true
        },
        {
          id: 502,
          role_name: '2',
          username: 'linken',
          create_time: 1486720211,
          mobile: '1213213123',
          email: 'asdf@qq.com',
          mg_state: false
        },
        {
          id: 508,
          role_name: '',
          username: 'asdf1',
          create_time: 1511853015,
          mobile: '123123',
          email: 'adfsa@qq.com',
          mg_state: true
        },
        {
          id: 509,
          role_name: 'test',
          username: 'asdf123',
          create_time: 1511853353,
          mobile: '1111',
          email: 'asdf@qq.com',
          mg_state: false
        }
      ],
      res: {
        data: {
          total: 4,
          pagenum: 1,
          users: [
            {
              id: 500,
              role_name: '',
              username: 'admin',
              create_time: 1486720211,
              mobile: '12345678',
              email: 'adsfad@qq.com',
              mg_state: true
            },
            {
              id: 502,
              role_name: '2',
              username: 'linken',
              create_time: 1486720211,
              mobile: '1213213123',
              email: 'asdf@qq.com',
              mg_state: false
            },
            {
              id: 508,
              role_name: '',
              username: 'asdf1',
              create_time: 1511853015,
              mobile: '123123',
              email: 'adfsa@qq.com',
              mg_state: true
            },
            {
              id: 509,
              role_name: 'test',
              username: 'asdf123',
              create_time: 1511853353,
              mobile: '1111',
              email: 'asdf@qq.com',
              mg_state: false
            }
          ]
        },
        meta: {
          msg: '',
          status: 200
        }
      },
      total: 4,
      value: '100',
      dialogVisible: false,
      addUserForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      userRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ],
        email: [
          {
            type: 'email',
            message: '',
            trigger: 'blur'
          }
        ],
        mobile: [
          {
            pattern: /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      // 
      editDialogVisible: false,
      // form
      editUserForm: {},
      // 
      assignDialogVisible: false,
      userInfo: {},
      roleList: [],
      //  select
      selectRoleId:null
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      var { data: res } = await this.$axios.get('users', {
        params: this.params
      })
      console.log(res)
      // var res = this.res
      this.userList = res.data.users
      this.total = res.data.total
      console.log(res.data)
    },
    // 
    handleSizeChange(val) {
      this.params.pagesize = val
      this.getUserList()
    },
    // 
    handleCurrentChange(val) {
      this.params.pagenum = val
      this.getUserList()
    },
    // switchchange
    async userStateChange(userInfo) {
      var { data: res } = await this.$axios.put(
        `users/${userInfo.row.id}/state/${userInfo.row.mg_state}`
      )
      if (res.meta.status !== 200) {
        userInfo.row.mg_state = !userInfo.row.mg_state
        return this.$message.error('')
      }

      this.$message({
        type: 'success',
        message: ''
      })
    },
    searchGetUserList() {
      this.params.pagenum = 1
      this.getUserList()
    },
    resetDialogForm() {
      this.$refs.addUserForm.resetFields()
    },
    // 
    addUserSubmit() {
      // 
      this.$refs.addUserForm.validate(async valid => {
        if (!valid) return this.$message.error('')
        var { data: res } = await this.$axios.post('users', this.addUserForm)
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)

        this.$message({
          message: '',
          type: 'success',
          showClose: true
        })
        this.dialogVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    showEditDialog(row) {
      this.editDialogVisible = true
      this.editUserForm = row
    },
    // 
    editUserFormSubmit() {
      this.$refs.editUserForm.validate(async valid => {
        if (!valid) return this.$message.error('')
        let form = {
          id: this.editUserForm.id,
          email: this.editUserForm.email,
          mobile: this.editUserForm.mobile
        }
        var { data: res } = await this.$axios.put(
          `users/${this.editUserForm.id}`,
          form
        )
        if (res.meta.status !== 200) return this.$message.error('')
        // form
        this.editDialogVisible = false
        this.$message.success('')
        //  
        this.userList.some((item, index) => {
          if (item.id === res.data.id) {
            item = res.data
            return true
          }
        })
      })
    },
    //
    delUser(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          var { data: res } = await this.$axios.delete('users/' + id)
          if (res.meta.status !== 200) return this.$message.error('')
          this.$message.success('')
          this.getUserList()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // dialog
    async showAssignRoleDialog(userInfo) {
      this.userInfo = userInfo
      let { data: res } = await this.$axios.get('roles')
      if (res.meta.status !== 200) return this.$message.error('')
      this.roleList = res.data
      this.assignDialogVisible = true
    },
    // 
    async assignRole(){
      if(!this.selectRoleId){
        return this.$message.error('')
      }
      let {data:res}=await this.$axios.put(`users/${this.userInfo.id}/role`,{
        rids:this.selectRoleId
      })
      if(res.meta.status!==200) return this.$message.error('')
      this.$message.success('')
      this.getUserList()
      this.assignDialogVisible=false
    }
  }
}
</script>
<style lang="less" scoped>
.operateColumnButton {
  margin-left: 10px !important;
}
.el-button {
  margin-top: 5px;
}
</style>
