<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-alert title="" type="info" center show-icon :closable="false"></el-alert>
      <el-steps :space="200" :active="active-0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!-- tabs -->
      <el-form
        :model="addGoodsFormModel"
        ref="addGoodsFormRef"
        :rules="addGoodsFormRules"
        label-width="100px"
        label-position="top"
      >
        <el-tabs v-model="active" tab-position="left">
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addGoodsFormModel.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addGoodsFormModel.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addGoodsFormModel.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addGoodsFormModel.goods_number" type="number"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1"></el-tab-pane>
          <el-tab-pane label="" name="2"></el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              action="http://127.0.0.1:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="uploadSuccess"
              list-type="picture"
              :headers="uploadHeaders"
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <quill-editor
              ref="myQuillEditor"
              v-model="addGoodsFormModel.goods_introduce"
              :options="editorOption"
              @blur="onEditorBlur($event)"
              @focus="onEditorFocus($event)"
              @ready="onEditorReady($event)"
            ></quill-editor>
            <el-button type="primary"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="previewVisibile" width="60%">
      <img :src="this.previewUrl" alt class="previewImg" />
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: 0,
      addGoodsFormModel: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        pics: [],
        // 
        goods_introduce:''
      },
      addGoodsFormRules: {
        goods_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_price: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_weight: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_number: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      // 
      uploadHeaders: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      previewUrl: '',
      // dialog 
      previewVisibile: false
    }
  },
  methods: {
    // 
    handlePreview(file) {
      this.previewUrl = file.response.data.url
      this.previewVisibile = true
    },
    // 
    handleRemove(file) {
      let pic = file.response.data.tmp_path
      this.addGoodsFormModel.pics.some((item, i) => {
        console.log(item.pic)
        if (item.pic == pic) {
          this.addGoodsFormModel.pics.splice(i, 1)
          return true
        }
      })
      console.log(this.addGoodsFormModel.pics)
    },
    uploadSuccess(res) {
      let obj = {
        pic: res.data.tmp_path
      }
      this.addGoodsFormModel.pics.push(obj)
    }
  }
}
</script>
<style lang="less" scoped>
.el-tabs {
  margin-top: 15px;
}
.previewImg {
  position: relative;
  left: 50%;
  transform: translate(-50%);
  width: 60%;
}
.el-button{
  margin-top: 15px
}
</style>