      this.roleList = res.data
    },
    // idtag
    removeTagById(row, rightId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          var { data: res } = await this.$axios.delete(
            `roles/${row.id}/rights/${rightId}`
          )
          if (res.meta.status !== 200) return this.$message.error('')

          row.children = res.data
          this.$message({
            type: 'success',
            message: '!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    async showAssignRightDialog(row) {
      // id
      this.roleId = row.id
      // tree
      var { data: res } = await this.$axios.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error('')
      this.rightsList = res.data
      // tree
      this.defaultCheckedKeys = []
      this.getLeafKey(row)
      this.assignRightDialog = true
    },
    getLeafKey(node) {
      if (!node.children) {
        return this.defaultCheckedKeys.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKey(item)
      })
    },
    // 
    async assignRight() {
      var keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      keys = keys.join(',')
      var { data: res } = await this.$axios.post(
        `roles/${this.roleId}/rights`,
        {
          rids: keys
        }
      )
      if (res.meta.status !== 200) return this.$message.error('')
      this.getRolesList()
      this.$message.success('')
      this.assignRightDialog = false
    }
